import React, { useState } from 'react';
import { Card } from 'primereact/card';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { InputTextarea } from 'primereact/inputtextarea';
import { convertFigmaToPrimeReact, extractFigmaFileKey, extractNodeId } from '../utils/figmaToPrimeReact';

const FigmaComponentBuilder = () => {
  const [figmaUrl, setFigmaUrl] = useState('');
  const [figmaData, setFigmaData] = useState(null);
  const [generatedComponent, setGeneratedComponent] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [mcpStatus, setMcpStatus] = useState('checking');

  const handleFigmaUrlChange = (e) => {
    setFigmaUrl(e.target.value);
    setError('');
  };

  const extractFigmaInfo = () => {
    const fileKey = extractFigmaFileKey(figmaUrl);
    const nodeId = extractNodeId(figmaUrl);
    
    if (!fileKey) {
      setError('Invalid Figma URL. Please provide a valid Figma file URL.');
      return null;
    }
    
    return { fileKey, nodeId };
  };

  const fetchFigmaData = async () => {
    setLoading(true);
    setError('');
    
    try {
      const figmaInfo = extractFigmaInfo();
      if (!figmaInfo) {
        setLoading(false);
        return;
      }

      console.log('Fetching Figma data via MCP for:', figmaInfo);
      
      // Use the Figma Developer MCP server
      const mcpResponse = await fetch('http://localhost:3333/mcp', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          jsonrpc: '2.0',
          id: 1,
          method: 'tools/call',
          params: {
            name: 'get_figma_file',
            arguments: {
              file_key: figmaInfo.fileKey
            }
          }
        })
      });

      if (!mcpResponse.ok) {
        throw new Error(`MCP Server error: ${mcpResponse.status} ${mcpResponse.statusText}`);
      }

      const mcpData = await mcpResponse.json();
      
      if (mcpData.error) {
        throw new Error(`MCP Error: ${mcpData.error.message}`);
      }

      if (!mcpData.result || !mcpData.result.content) {
        throw new Error('No data received from MCP server');
      }

      const figmaData = JSON.parse(mcpData.result.content[0].text);
      
      // If we have a specific node ID, get that node's data
      if (figmaInfo.nodeId) {
        const nodeMcpResponse = await fetch('http://localhost:3333/mcp', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            jsonrpc: '2.0',
            id: 2,
            method: 'tools/call',
            params: {
              name: 'get_figma_node',
              arguments: {
                file_key: figmaInfo.fileKey,
                node_id: figmaInfo.nodeId
              }
            }
          })
        });

        if (nodeMcpResponse.ok) {
          const nodeMcpData = await nodeMcpResponse.json();
          if (nodeMcpData.result && nodeMcpData.result.content) {
            const nodeData = JSON.parse(nodeMcpData.result.content[0].text);
            if (nodeData) {
              setFigmaData(nodeData);
              generateComponent(nodeData);
              return;
            }
          }
        }
      }

      // Fallback to the main document
      setFigmaData(figmaData.document);
      generateComponent(figmaData.document);
      
    } catch (err) {
      setError(`Failed to fetch Figma data via MCP: ${err.message}. Make sure the MCP server is running.`);
      console.error('Error fetching Figma data:', err);
    } finally {
      setLoading(false);
    }
  };

  const generateComponent = (data) => {
    try {
      const component = convertFigmaToPrimeReact(data);
      
      // Convert React element to string representation
      const componentString = `
import React from 'react';
import { ${getRequiredImports(data)} } from 'primereact/primereact';

export const GeneratedComponent = () => {
  return (
    ${componentToJSX(component)}
  );
};
      `.trim();
      
      setGeneratedComponent(componentString);
    } catch (err) {
      setError('Failed to generate component. Please check the Figma data.');
      console.error('Error generating component:', err);
    }
  };

  const getRequiredImports = (data) => {
    const imports = new Set();
    
    const extractImports = (node) => {
      if (node.type === 'RECTANGLE') {
        if (node.name.toLowerCase().includes('button')) imports.add('Button');
        if (node.name.toLowerCase().includes('input')) imports.add('InputText');
      }
      if (node.type === 'FRAME' && node.name.toLowerCase().includes('card')) {
        imports.add('Card');
      }
      if (node.children) {
        node.children.forEach(extractImports);
      }
    };
    
    extractImports(data);
    return Array.from(imports).join(', ');
  };

  const componentToJSX = (component) => {
    // This is a simplified conversion - in a real implementation,
    // you'd need a more sophisticated React element to JSX string converter
    return '<div>Generated Component</div>';
  };

  const copyToClipboard = () => {
    navigator.clipboard.writeText(generatedComponent);
  };

  // Check MCP server status on component mount
  React.useEffect(() => {
    const checkMcpStatus = async () => {
      try {
        const response = await fetch('http://localhost:3333/health');
        if (response.ok) {
          setMcpStatus('connected');
        } else {
          setMcpStatus('error');
        }
      } catch (error) {
        setMcpStatus('error');
      }
    };

    checkMcpStatus();
  }, []);

  return (
    <div className="p-4">
      <Card title="Figma to PrimeReact Component Builder" className="mb-4">
        {/* MCP Server Status */}
        <div className="mb-3">
          <div className="flex align-items-center gap-2">
            <span className="text-sm font-medium">MCP Server Status:</span>
            {mcpStatus === 'checking' && (
              <span className="flex align-items-center gap-1 text-blue-600">
                <i className="pi pi-spin pi-spinner"></i>
                Checking...
              </span>
            )}
            {mcpStatus === 'connected' && (
              <span className="flex align-items-center gap-1 text-green-600">
                <i className="pi pi-check-circle"></i>
                Connected
              </span>
            )}
            {mcpStatus === 'error' && (
              <span className="flex align-items-center gap-1 text-red-600">
                <i className="pi pi-exclamation-triangle"></i>
                Not Connected
              </span>
            )}
          </div>
          {mcpStatus === 'error' && (
            <p className="text-xs text-red-600 mt-1">
              Make sure the Figma Developer MCP server is running on port 3333
            </p>
          )}
        </div>
        <div className="grid">
          <div className="col-12">
            <label htmlFor="figmaUrl" className="block text-sm font-medium mb-2">
              Figma URL
            </label>
            <InputText
              id="figmaUrl"
              value={figmaUrl}
              onChange={handleFigmaUrlChange}
              placeholder="https://figma.com/file/..."
              className="w-full mb-3"
            />
            <Button
              label="Fetch Figma Data"
              onClick={fetchFigmaData}
              loading={loading}
              disabled={!figmaUrl}
              className="mb-3"
            />
          </div>
        </div>

        {error && (
          <div className="p-3 mb-3 border-round surface-100 border-1 border-red-200 bg-red-50">
            <i className="pi pi-exclamation-triangle text-red-500 mr-2"></i>
            {error}
          </div>
        )}

        {figmaData && (
          <div className="grid">
            <div className="col-12 md:col-6">
              <Card title="Figma Data" className="h-full">
                <pre className="text-xs overflow-auto" style={{ maxHeight: '300px' }}>
                  {JSON.stringify(figmaData, null, 2)}
                </pre>
              </Card>
            </div>
            <div className="col-12 md:col-6">
              <Card 
                title="Generated PrimeReact Component" 
                className="h-full"
                header={
                  <div className="flex justify-content-between align-items-center">
                    <span>Generated Component</span>
                    <Button
                      icon="pi pi-copy"
                      onClick={copyToClipboard}
                      size="small"
                      text
                    />
                  </div>
                }
              >
                <InputTextarea
                  value={generatedComponent}
                  readOnly
                  rows={15}
                  className="w-full font-mono text-xs"
                />
              </Card>
            </div>
          </div>
        )}
      </Card>

      <Card title="How to Use" className="mb-4">
        <div className="grid">
          <div className="col-12 md:col-6">
            <h3>Step 1: Get Figma URL</h3>
            <p className="text-sm text-600">
              1. Open your Figma design<br/>
              2. Select the component you want to convert<br/>
              3. Copy the URL from your browser
            </p>
          </div>
          <div className="col-12 md:col-6">
            <h3>Step 2: Generate Component</h3>
            <p className="text-sm text-600">
              1. Paste the Figma URL above<br/>
              2. Click "Fetch Figma Data"<br/>
              3. Review the generated PrimeReact component
            </p>
          </div>
        </div>
      </Card>

      <Card title="Supported Components">
        <div className="grid">
          <div className="col-12 md:col-4">
            <h4>Basic Components</h4>
            <ul className="text-sm">
              <li>Buttons (with variants)</li>
              <li>Input fields</li>
              <li>Text elements</li>
              <li>Cards</li>
            </ul>
          </div>
          <div className="col-12 md:col-4">
            <h4>Layout Components</h4>
            <ul className="text-sm">
              <li>Frames</li>
              <li>Groups</li>
              <li>Containers</li>
            </ul>
          </div>
          <div className="col-12 md:col-4">
            <h4>Styling</h4>
            <ul className="text-sm">
              <li>Colors and fills</li>
              <li>Borders and strokes</li>
              <li>Shadows and effects</li>
              <li>Typography</li>
            </ul>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default FigmaComponentBuilder; 