import React, { useState } from 'react';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import { InputNumber } from 'primereact/inputnumber';
import { Password } from 'primereact/password';
import { Checkbox } from 'primereact/checkbox';
import { RadioButton } from 'primereact/radiobutton';
import { Dropdown } from 'primereact/dropdown';
import PageHeader from './PageHeader';

export default function PrimeComponentShowcase() {
  const [dialogVisible, setDialogVisible] = useState(false);
  const [checkbox, setCheckbox] = useState(false);
  const [radio, setRadio] = useState('A');
  const [dropdown, setDropdown] = useState(null);
  const [number, setNumber] = useState(null);
  const [password, setPassword] = useState('');
  const dropdownOptions = [
    { label: 'Option 1', value: '1' },
    { label: 'Option 2', value: '2' },
  ];

  return (
    <div style={{ padding: '2rem', color: 'var(--text-color)' }}>
      <PageHeader
        title="PrimeReact Component Showcase"
        onSearch={value => console.log('Search:', value)}
        onFilter={() => alert('Filter clicked')}
      />
      <div style={{ marginTop: '2rem' }}>
        <section style={{ marginBottom: '2rem' }}>
        <h3>Button Variants</h3>
        <div style={{ marginBottom: 12 }}>
          <span style={{ fontWeight: 500, marginRight: 8 }}>Small:</span>
          <Button label="Primary" size="small" className="p-button" style={{ marginRight: 8 }} />
          <Button label="Secondary" size="small" className="p-button-secondary" style={{ marginRight: 8 }} />
          <Button label="Success" size="small" className="p-button-success" style={{ marginRight: 8 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <span style={{ fontWeight: 500, marginRight: 8 }}>Default:</span>
          <Button label="Primary" className="p-button" style={{ marginRight: 8 }} />
          <Button label="Secondary" className="p-button-secondary" style={{ marginRight: 8 }} />
          <Button label="Success" className="p-button-success" style={{ marginRight: 8 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <span style={{ fontWeight: 500, marginRight: 8 }}>Large:</span>
          <Button label="Primary" size="large" className="p-button" style={{ marginRight: 8 }} />
          <Button label="Secondary" size="large" className="p-button-secondary" style={{ marginRight: 8 }} />
          <Button label="Success" size="large" className="p-button-success" style={{ marginRight: 8 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <Button label="Info" className="p-button-info" style={{ marginRight: 8 }} />
          <Button label="Warning" className="p-button-warning" style={{ marginRight: 8 }} />
          <Button label="Danger" className="p-button-danger" style={{ marginRight: 8 }} />
          <Button label="Text" className="p-button-text" style={{ marginRight: 8 }} />
          <Button label="Outlined" className="p-button-outlined" style={{ marginRight: 8 }} />
        </div>
        </section>
        <section style={{ marginBottom: '2rem' }}>
        <h3>Dialog Variants</h3>
        <Button label="Show Dialog" onClick={() => setDialogVisible(true)} />
        <Dialog header="Sample Dialog" visible={dialogVisible} style={{ width: '30vw' }} onHide={() => setDialogVisible(false)}>
          <p>This is a PrimeReact Dialog.</p>
          <Button label="Close" onClick={() => setDialogVisible(false)} className="p-button-text" />
        </Dialog>
        </section>
        <section>
        <h3>Form Inputs</h3>
        <div style={{ marginBottom: 12 }}>
          <label htmlFor="inputText" style={{ display: 'block', marginBottom: 4 }}>Text Input</label>
          <InputText id="inputText" placeholder="Enter text" style={{ width: 240 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <label htmlFor="inputNumber" style={{ display: 'block', marginBottom: 4 }}>Number Input</label>
          <InputNumber id="inputNumber" value={number} onValueChange={e => setNumber(e.value)} style={{ width: 240 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <label htmlFor="password" style={{ display: 'block', marginBottom: 4 }}>Password</label>
          <Password id="password" value={password} onChange={e => setPassword(e.target.value)} toggleMask style={{ width: 240 }} />
        </div>
        <div style={{ marginBottom: 12 }}>
          <label style={{ display: 'block', marginBottom: 4 }}>Checkbox</label>
          <Checkbox inputId="cb1" checked={checkbox} onChange={e => setCheckbox(e.checked)} />
          <label htmlFor="cb1" style={{ marginLeft: 8 }}>Check me</label>
        </div>
        <div style={{ marginBottom: 12 }}>
          <label style={{ display: 'block', marginBottom: 4 }}>Radio Buttons</label>
          <RadioButton inputId="rb1" name="rb" value="A" onChange={e => setRadio(e.value)} checked={radio === 'A'} />
          <label htmlFor="rb1" style={{ marginRight: 16, marginLeft: 4 }}>A</label>
          <RadioButton inputId="rb2" name="rb" value="B" onChange={e => setRadio(e.value)} checked={radio === 'B'} />
          <label htmlFor="rb2" style={{ marginLeft: 4 }}>B</label>
        </div>
        <div style={{ marginBottom: 12 }}>
          <label htmlFor="dropdown" style={{ display: 'block', marginBottom: 4 }}>Dropdown</label>
          <Dropdown id="dropdown" value={dropdown} options={dropdownOptions} onChange={e => setDropdown(e.value)} placeholder="Select" style={{ width: 240 }} />
        </div>
        </section>
      </div>
    </div>
  );
}
